<template>
    <div v-if="carousel">
        <el-carousel :interval="4000" type="card" :height="height" indicator-position="none">
            <el-carousel-item v-for="(song ,index) in carousel" :key="index" @click.native="clickCarousel(song.id)">
                <el-image

                        style="width: 100%; height: 100%"
                        :src="song.pic"
                        fit="cover">
                    <span>名字</span>: <span>{{song.name}}</span>
                </el-image>
            </el-carousel-item>
        </el-carousel>
    </div>

</template>
<style>
    .el-carousel__item h3 {
        color: #475669;
        font-size: 14px;
        opacity: 0.75;
        line-height: 200px;
        margin: 0;
    }

    .el-carousel__item:nth-child(2n) {
        background-color: #99a9bf;
    }

    .el-carousel__item:nth-child(2n+1) {
        background-color: #d3dce6;
    }
</style>
<script>

    export default{
        props: ["carousel","height"],
        data(){
            return{

            }
        },
        methods: {
            clickCarousel (songListId) {
                this.musicApi.songlist(songListId).then(data => {
                    this.$emit("update:songlist",data);
                })
            }
        },
    }
</script>
