<template>
    <div  style="white-space: nowrap;overflow: auto;-webkit-overflow-scrolling: touch" >

        <el-menu :default-active="activeIndex"
                 class="el-menu-demo"
                 mode="horizontal" @select="handleSelect"
                style="width: 550px">
            <el-submenu :index="sortIndex + ''" v-for="(sortItem,sortIndex) in sortArr" :key="sortIndex">
                <template slot="title">{{sortItem.categoryGroupName}}</template>
                <el-menu-item
                        :index="sortIndex + '-' + categoryItemIndex "
                        v-for="(category,categoryItemIndex) in sortItem.items"
                        :key="categoryItemIndex"
                        @click="selectCategory(category.categoryId)"
                >
                    {{category.categoryName}}
                </el-menu-item>
            </el-submenu>
        </el-menu>
    </div>
</template>

<script>
    export default {
        name: "Header",
        data () {
            return {
                activeIndex: "0-0",
                sortArr: []
            }
        },
        mounted () {
            var self = this;
            this.musicApi.songListCategory().then(data => {
                self.sortArr = data.data.data;
                self.$emit("update:ch-category",self.sortArr[0].items[0].categoryId )
            })
        },
        methods: {
            handleSelect () {

            },
            selectCategory(id){
                this.$emit("update:ch-category",id)
            }
        }
    }
</script>

