<template>

    <div ref="asideSongScroller"
         style="height: 100%;position: relative;overflow: hidden">
        <div class="content">
            <div v-if="songList"
                 style="overflow: hidden;text-overflow: ellipsis;white-space: nowrap">
                <div  v-for="(song,index )  in songList.songs" :key="index"
                      @click="handleSongItem(index)"
                      :class="[playerIndex === index ? 'player-item': '','list-item']"
                      style="position: relative">
                    <el-image
                            style="width: 45px; height: 45px"
                            :src="song.pic"
                            fit="cover"></el-image>
                    <span style="padding-left: 30px">{{song.singer}}</span>:
                    <span>{{song.name}}</span>
                    <i class="el-icon-video-play "
                       v-if="playerIndex === index "
                       style="float: right;margin: 19px 100px 0 0">

                    </i>

                </div>
            </div>
            <div v-else class="list-empty-icon">
                <i class="el-icon-s-shop"></i><br/>
                播放列表为空,选择歌单
            </div>
        </div>

    </div>
</template>

<script>
    import {mapState} from "vuex";
    import BScroll from 'better-scroll'

    export default {
        name: "AudioList",
        props: ["songList"],
        data () {
            return {

            }
        },
        mounted () {
            const scroll = new BScroll(this.$refs.asideSongScroller ,{
                scrollbar: {
                    fade: true,
                    interactive: false // 1.8.0 新增
                },
                click: true,
                mouseWheel: true
            });
        },
        methods: {
            handleSongItem (songUrl) {
                this.$store.commit("updatePlayerIndex",songUrl);
            }
        },
        watch: {
            songList () {
                this.$store.commit("updateplayerList",this.songList);
            }
        },
        computed: mapState({
            playerIndex (state) {
                return state.playerIndex;
            }
        })
    }
</script>

<style scoped>
    .list-item{
        height: 50px;
        line-height: 50px;
        text-align: left;
        padding: 2px 15px;
    }
    .list-empty-icon{
        margin-top: 150px;
    }
    .list-item *{
        cursor: pointer;
    }
    .list-item:hover{
        background-color: #e5e9f2;
    }
    .list-item{
        box-sizing: border-box;
        border-bottom: 1px solid #d3dce6;
      }
    .list-item *, .list-item {
        cursor: pointer;
    }
    .player-item{
        background-color: #d3dce6;
    }
    .el-icon-video-play{
        position: absolute;
        left: 73px;
        top: 2px;
    }
</style>