<template xmlns:v-slot="http://www.w3.org/1999/XSL/Transform">
    <div id="app">
        <el-container style="height:100%">
            <el-aside width="400px" style="padding: 0" class="hidden-xs-only">
                <Aside :song-list="SongList" style="height: 100%"></Aside>
            </el-aside>
            <el-container>
                <el-header>
                    <Header :ch-category.sync="categoryId"></Header>
                </el-header>
                <el-main style="padding: 0">
                    <transition name="fade">
                        <router-view :song-list.sync="SongList" :category-id="categoryId"></router-view>
                    </transition>
                </el-main>
                <transition name="fade">
                    <div v-if="playerInfo">
                        <el-footer>
                            <AudioPlayer :player-info="playerInfo" @chindex="audioChIndex"></AudioPlayer>
                        </el-footer>
                    </div>
                </transition>

            </el-container>
        </el-container>
    </div>
</template>

<script>
    import Aside from './components/Aside.vue'
    import Header from './components/Header'
    import AudioPlayer from './components/AudioPlayer'
    import {mapState} from 'vuex'

    export default {
        name: 'app',
        data() {
            return {
                SongList: null,
                categoryId: null
            }
        },
        methods: {
            audioChIndex(page) {
                var currentIndex = this.$store.state.playerIndex;
                this.$store.commit("updatePlayerIndex", page ? ++currentIndex : --currentIndex);
            },
        },
        components: {
            Aside,
            Header,
            AudioPlayer
        },
        watch: {
            SongList() {
                this.$store.commit("updatePlayerIndex", 0);
            },
            categoryId() {

            }
        },
        computed: {
            ...mapState({
                playerInfo(state) {
                    if (state.playerList) {
                        return state.playerList.songs[state.playerIndex]
                    } else {
                        return null;
                    }
                }
            })
        }
    }
</script>

