const parseLrc = {
//     [ti:半阕相思]
// [ar:小时]
// [al:半阕相思]
// [by:]
// [offset:0]
// [00:00.00]半阕相思 - 小时
//    解析上边歌词格式
    getLrc(data) {
        var lrcData = {};
        var reg = new RegExp('(\\[)(' + '[0-9]{1,2}:[0-9]{1,2})' + '(\\.[0-9]{1,3}\\])(.*)(\\n.*|.*)', "g");
         f();
        function f() {
            var matchStr = reg.exec(data);
            if(matchStr){
                lrcData[matchStr[2]] = matchStr[4];
                f();
            }
        }
        return lrcData;
    }
};

export default parseLrc;