import Vue from 'vue'
import VueRouter from 'vue-router'
import SongMenu from './components/SongMenu'


Vue.config.productionTip = false
Vue.use(VueRouter)
const routes = [
    {
        path: '/SongMenu',
        components: {
            default: SongMenu,
        }
    },
    {path: '/', redirect: '/SongMenu'},
];
const router = new VueRouter({
    routes
})
export default router;