<template>
    <div>
        <transition name="el-fade-in-linear">
            <div v-if="playerInfo" >
                <div style="overflow: hidden">
                    <div style="float: left;width: 50px;line-height: 50px">
                        <el-image
                                style="width: 40px; height: 40px;vertical-align: middle"
                                :src="playerInfo.pic"
                                fit="cover"></el-image>
                    </div>
                    <div  style="float: left;;text-align: left;width: calc(100% - 50px)">
                        <div class=" text-overflow"
                             >
                            <span style="padding-left: 15px" >{{playerInfo.name}}</span>&nbsp;
                            <span >{{playTime}}</span>
                            <i class="el-icon-caret-left btn" @click="previousTrack" >上一首</i>
                            <span @click="play=!play" class="btn">
                            <i v-if="play" class="el-icon-video-pause btn"></i>
                            <i v-else class="el-icon-video-play btn"></i>
                           <span>{{play ? pauseText: playText }}</span>
                        </span>
                            <i class="el-icon-caret-right btn" @click="nextTrack" >下一首</i>
                        </div>
                        <div  class="text-overflow">
                            <span style="padding-left: 15px">{{lrcRows}}</span>
                        </div>
                    </div>
                </div>
                <audio :src="playerInfo.url" autoplay controls @ended="ended" @timeupdate="timeupdate($event)" style="display: none" ref="audio"></audio>
                <transition name="el-fade-in-linear">
                    <div style="height: 10px">
                        <div v-if="currentProgress" width="calc(100% - 100px)">
                            <el-progress :percentage="currentProgress" :show-text="showProgressText" ></el-progress>
                        </div>
                        <!--<div style="float: right">{{totalTime}}</div>-->
                    </div>

                </transition>
            </div>
        </transition>

    </div>
</template>

<script>
    import paresLrc from './../../util/parseLrc'
    export default {
        name: "AudioPlayer",
        props: ["playerInfo"],
        data () {
            return {
                currentTime: null,
                duration: null,
                showProgressText: false,
                play: true,
                playText: "播放",
                pauseText: "暂停",
                lrc: null,
                lrcRows: null
            }
        },
        mounted () {
            this.playerIndex = this.index;
            var self = this;
            window.onkeydown = function (e){
                if(e.keyCode === 32){
                    self.play = !self.play;
                }
                if(e.keyCode === 37){
                    self.$emit("chindex",false);
                }
                if(e.keyCode === 39){
                    self.$emit("chindex",true);
                }
                return false;
            }
           if(this.playerInfo){
               this.musicApi.lic(this.playerInfo.lrc).then((data) => {
                   var lrc = paresLrc.getLrc(data.data,formatSeconds(this.currentTime)) ;
                   if(Object.keys(lrc).length > 0){
                       self.lrc = lrc;
                   }else{
                       self.lrc = null;
                   }
               })
           }
        },
        methods: {
            ended () {
                this.$emit("chindex",true);
            },
            timeupdate (e) {
                if(this.lrc){
                    var lrcRows = this.lrc[formatSeconds(e.target.currentTime)];
                    if(lrcRows)this.lrcRows = lrcRows;
                }
                this.currentTime = e.target.currentTime;
                this.duration = e.target.duration;
            },
            nextTrack () {
                this.$emit("chindex",true);
            },
            previousTrack () {
                this.$emit("chindex",false);
            },

        },
        watch: {
            play () {
                var audio = this.$refs.audio;
                if(this.play){
                    audio.play();
                }else{
                    audio.pause();
                }
            },
            playerInfo(){
                var self = this;
                this.lrcRows = null;
                this.musicApi.lic(this.playerInfo.lrc).then((data) => {
                    var lrc = paresLrc.getLrc(data.data,formatSeconds(this.currentTime)) ;
                    if(Object.keys(lrc).length > 0){
                        self.lrc = lrc;
                    }else{
                        self.lrc = null;
                    }
                })
            },
        },
        computed :{
            playTime () {
                return formatSeconds(this.currentTime);
            },
            currentProgress ( ) {
                return + (this.currentTime / this.duration * 100).toFixed(2);
            },
            totalTime () {
                return formatSeconds(this.duration);
            }
        }
    }
    function formatSeconds(second) {

        var hourTime = 0;
        var minuteTime = 0;
        var secondTime = 0;
        if (second > 60) {  //如果秒数大于60
            minuteTime = Math.floor(second / 60);
            secondTime = Math.floor(second % 60);
            if (minuteTime >= 60) {  //如果分钟大于60
                hourTime = Math.floor(minuteTime / 60);
                minuteTime = Math.floor(minuteTime % 60);
            } else {
                hourTime = 0;
            }
        } else {
            hourTime = 0;
            minuteTime = 0;
            if (second == 60) {  //如果秒数等于60
                minuteTime = 1;
                secondTime = 0;
            } else {
                secondTime = second;
            }
        }
        function  addZero(time) {
            let str = Number(time).toFixed(0);
            if (time < 10) {
                str = '0' + Number(time).toFixed(0);
            }
            return str;
        }
        var timeResult = addZero(minuteTime) + ':' +  addZero(secondTime);
        return timeResult;
    }

</script>

<style scoped>
    .text-overflow {
        line-height: 25px;
        max-width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    .btn{
        cursor: pointer;
    }
    .btn:hover{
        opacity: 0.8;
    }
</style>
